
.body{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}.parent{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
}
.profilephoto{
    height: 350px;
    width: 350px;
    border-radius: 50%;
}
.page1{
    display: flex;
    height: 100vh;
    width: 100%;
    
}
.homepage{
    display: flex;
    background-color: rgb(239 239 239);  
    align-items: center;
    width: 100%;
    justify-content: space-around;
    flex-wrap: wrap;
}
.sidenav{
    width: 25%;
    display: flex;
    flex-direction: column;
    align-items:baseline;
    justify-content: center;
    /* height: 45rem; */
    padding-left: 50px;
    border-right: 0.1px solid #cdc7c7;
}
@media screen and (max-width:950px) {
    .page1{
        display: block;
    }
    .sidenav{
        width: 100%;
        flex-direction: row;
        /* height: auto; */
        align-items: center;
        padding: 0;
    }
    .folio{
        padding-bottom: 0 !important;
    }
    .sidenav{
        height: 70px;
    }
    .navbar{
        flex-direction: row !important;
        width: 70%;

    }
    .copyR,.cName{
        display: none;
    }
    .homepage{
        height: calc(100vh - 70px);
    }
}
@media screen  and (max-width:540px){
    .navbar{
        display: none !important;
    }
    .profilephoto{
        width:320px;
        
    }
    
}
.navbar{
    display: flex;
    flex-direction: column;
    height: 40%;
    justify-content: space-evenly;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: larger;
    cursor: pointer;
    color: rgb(61, 58, 58);
}
.navs:hover{
    color: black;
    cursor: pointer;
    font-weight: 550;
}
.folio{
    padding-bottom: 25px;
    font-weight: bold;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: xx-large;
    cursor: pointer;
}
.copyR{
    margin-top: 10px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size:medium;
    color: grey;
}
.cName{
    color: rgb(87, 84, 84);
    font-weight: 500;
}
.name{
    font-size: xxx-large;
    font-weight: 600;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

}
.introduction{
 display: flex;
 flex-direction: column;
 flex-wrap: wrap;
 align-items: center;
}
.jobProfile{
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    color:rgb(87, 84, 84);
    font-size: large;
}
